@use "../variables.scss";

.addition {
    padding: 13px 0 18px;
    display: flex;
    flex-flow: column nowrap;

    @media (min-width: variables.$tablet-width) {
        padding: 26px 38px 27px;
        flex-flow: row wrap;
        justify-content: space-between;
        align-items: baseline;
    }

    @media (min-width: variables.$desktop-width) {
        padding: 16px 1px 15px;
        display: grid;
        grid-template-columns: 326px 324px 244px;
    }
}

.addition__name {
    margin: 0;
    margin-bottom: 13px;
    padding: 0;
    font-family: variables.$primary-font;
    font-size: 16px;
    line-height: 16px;
    color: variables.$special-dark-grey-2;
    text-transform: uppercase;

    @media (min-width: variables.$tablet-width) {
        font-size: 20px;
        line-height: 20px;
        margin-bottom: 0;
    }
}

.addition__info {
    margin: 0;
    margin-bottom: 15px;
    padding: 0;
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
    font-family: variables.$secondary-font;
    font-size: 14px;
    line-height: 14px;
    font-weight: 400;
    color: variables.$special-dark-grey;

    @media (min-width: variables.$tablet-width) {
        font-size: 16px;
        line-height: 20px;
        margin-bottom: 0;
        text-align: right;
    }

    @media (min-width: variables.$desktop-width) {
        display: grid;
        grid-template-columns: 242px 1fr;
        text-align: left;
    }
}

.addition__amount {
    @media (min-width: variables.$tablet-width) {
        position: relative;
        left: -2px;
    }
}

.addition__price {
    text-transform: uppercase;

    @media (min-width: variables.$tablet-width) {
        width: 90px;
    }

    @media (min-width: variables.$desktop-width) {
        width: auto;
    }
}

.addition__button {
    @media (min-width: variables.$tablet-width) {
        width: 100%;
        margin-top: 21px;
    }

    @media (min-width: variables.$desktop-width) {
        width: auto;
        margin-top: 0;
    }
}
