@font-face {
  font-family: "Oswald";
  src: local("Oswald"), url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "Oswald";
  src: local("Oswald"), url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
  font-weight: 700;
  font-style: normal;
}
.addition {
  padding: 13px 0 18px;
  display: flex;
  flex-flow: column nowrap;
}
@media (min-width: 768px) {
  .addition {
    padding: 26px 38px 27px;
    flex-flow: row wrap;
    justify-content: space-between;
    align-items: baseline;
  }
}
@media (min-width: 1440px) {
  .addition {
    padding: 16px 1px 15px;
    display: grid;
    grid-template-columns: 326px 324px 244px;
  }
}

.addition__name {
  margin: 0;
  margin-bottom: 13px;
  padding: 0;
  font-family: "Oswald", arial, sans-serif;
  font-size: 16px;
  line-height: 16px;
  color: #222222;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .addition__name {
    font-size: 20px;
    line-height: 20px;
    margin-bottom: 0;
  }
}

.addition__info {
  margin: 0;
  margin-bottom: 15px;
  padding: 0;
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  font-family: arial, sans-serif;
  font-size: 14px;
  line-height: 14px;
  font-weight: 400;
  color: #444444;
}
@media (min-width: 768px) {
  .addition__info {
    font-size: 16px;
    line-height: 20px;
    margin-bottom: 0;
    text-align: right;
  }
}
@media (min-width: 1440px) {
  .addition__info {
    display: grid;
    grid-template-columns: 242px 1fr;
    text-align: left;
  }
}

@media (min-width: 768px) {
  .addition__amount {
    position: relative;
    left: -2px;
  }
}

.addition__price {
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .addition__price {
    width: 90px;
  }
}
@media (min-width: 1440px) {
  .addition__price {
    width: auto;
  }
}

@media (min-width: 768px) {
  .addition__button {
    width: 100%;
    margin-top: 21px;
  }
}
@media (min-width: 1440px) {
  .addition__button {
    width: auto;
    margin-top: 0;
  }
}

.additions {
  position: relative;
}
.additions::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  top: 15px;
  left: 0;
  background-color: #68b738;
  z-index: -1;
}
@media (min-width: 768px) {
  .additions::before {
    top: 23px;
  }
}

.additions__container {
  display: flex;
  flex-flow: column nowrap;
  padding: 0 20px;
  margin-bottom: 17px;
}
@media (min-width: 768px) {
  .additions__container {
    padding: 0 30px;
    margin-bottom: 76px;
  }
}
@media (min-width: 1440px) {
  .additions__container {
    padding: 0 110px;
    flex-flow: row nowrap;
    justify-content: space-between;
    margin-bottom: 86px;
  }
}

.additions__title {
  margin: 0;
  margin-bottom: 35px;
  padding: 0 20px;
  font-family: "Oswald", arial, sans-serif;
  font-size: 24px;
  line-height: 24px;
  font-weight: 400;
  color: #000000;
  background-color: #ffffff;
